import PizZipUtils from 'pizzip/utils';
import { Stream } from "stream";
import Docxtemplater from "docxtemplater";

import dayjs from 'dayjs';
import localizedFormat from 'dayjs/plugin/localizedFormat';

dayjs.extend(localizedFormat);


const defArr: any[] = [];
const photosDefArr: any[] = [];
const imagesDefArr: any[] = [];
const imageUploadsDefArr: any[] = [];
const userInvestigationFactsListDefArr: any[] = [];

export default defineComponent({
  data() {

    return {
      form: {
        userTodayDate: '',

        fileName: '',
        deviceMake: '',
        deviceModel: '',
        deviceColor: '',
        otherDeviceIdentifiers: '',
        evidenceSought: defArr,
        startDate: '',
        endDate: '',
        offenseType: '',
        violationCode: '',
        caseNumber: '',
        userPosition: '',
        userUnit: '',
        hasUnit: false,
        userBackground: '',
        userDepartment: '',
        userDepartmentAddress: '',

        userInvestigationFacts: '',
        userProbableWebRelatedDevice: '',
        userProbableCloudStorage: '',
        userProbablePeerToPeer: '',
        userProbableCommunication: '',
        userProbableSocialNetworking: '',
        userProbableMediaEvidence: '',
        userProbableEmailCalendar: '',
        userProbableDocuments: '',
        userProbableApplicationUsage: '',
        userProbableOperatingSystem: '',
        userProbableEncryptionCredentials: '',
        userProbableConnectedDevices: '',
        userProbableLocationTravel: '',

        userEmail: '',
        userName: '',
        userSignature: '',

        userPronouns: '',
        pronouns1: '',
        pronouns2: '',

        courtType: '',
        county: '',
        state: '',
        stateUpper: ''
      },
      photos: photosDefArr,
      monthsArr: ['Januray', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
      stateOptions: [
  'Alabama', 'Alaska', 'Arizona', 'Arkansas', 'California', 'Colorado', 'Connecticut', 'Delaware', 'Florida', 'Georgia', 'Hawaii', 'Idaho', 'Illinois', 'Indiana', 'Iowa', 'Kansas', 'Kentucky', 'Louisiana', 'Maine', 'Maryland', 'Massachusetts', 'Michigan', 'Minnesota', 'Mississippi', 'Missouri', "Montana", "Nebraska", "Nevada", "New Hampshire", "New Jersey", "New Mexico", "New York", "North Carolina", "North Dakota", "Ohio", "Oklahoma", "Oregon", "Pennsylvania", "Rhode Island", "South Carolina", "South Dakota", "Tennessee", "Texas", "Utah", "Vermont", "Virginia", "Washington", "West Virginia", "Wisconsin", "Wyoming"],
      images: imagesDefArr,
      imageUploads: imageUploadsDefArr,
      evidenceSoughtItems: [
        'Web Related evidence',
        'Communication evidence',
        'Social Networking evidence',
        'Media evidence',
        'Email & Calendar evidence',
        'Documents evidence',
        'Cloud Storage evidence',
        'Application Usage evidence',
        'Operating System evidence',
        'Encryption & Credentials',
        'Connected Devices evidence',
        'Location & Travel evidence',
        'Peer to Peer evidence'
      ],
      imageProps: {height: 150, center: true}
    }
  },
  computed: {},
  created() {
    this.form.userTodayDate = dayjs().format('YYYY-MM-DD');
  },
  methods: {
    getDoc() {
      this.createAndSaveDocument();
    },

    deleteSelectedImg: function (selectedPic: any) {
      this.photos = this.photos.filter((item: any) => selectedPic != item.name)
      return;
    },

    loadFile(url: any, callback: any) {
      JSzipUtils.getBinaryContent(url, callback);
    },

    onChange(e: any) {
      const that = this
      const file = e.target.files[0]

      that.photos.push({
        name: file.name,
        pic: file,
        url: URL.createObjectURL(file)
      });
    },
    getPreview(item: any) {
      return URL.createObjectURL(item);
    },
    clearImageUploads() {
      // this.$refs['imageUploads'].reset();
    },
    createAndSaveDocument() {
      const that = this;

      // Check that at least one evidence item has been selected.
      if(!that.form.evidenceSought.length) {
        return that.$bvToast.toast("Please enter at least one evidence request!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      }

      // Please enter start/end dates.
      if(!that.form.startDate || !that.form.endDate) {
        return that.$bvToast.toast("Please enter start/end dates!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      }

      // Fetch the image URLs for the image module.
      that.images = [];
      if(that.imageUploads.length) {
        that.imageUploads.forEach((item) => {
          that.images.push({ image: URL.createObjectURL(item) })
        });
      }

      // If any of these are not selected, add 'Not Sought' for clarity.
      if(that.form.evidenceSought.indexOf('Web Related evidence') >= 0 && !that.form.userProbableWebRelatedDevice) {
        return that.$bvToast.toast("Please enter Web Related evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableWebRelatedDevice) {
        that.form.userProbableWebRelatedDevice = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Cloud Storage evidence') >= 0 && !that.form.userProbableCloudStorage) {
        return that.$bvToast.toast("Please enter Cloud Storage Related evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableCloudStorage) {
        that.form.userProbableCloudStorage = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Peer to Peer evidence') >= 0 && !that.form.userProbablePeerToPeer) {
        return that.$bvToast.toast("Please enter Peer to Peer Related evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbablePeerToPeer) {
        that.form.userProbablePeerToPeer = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Communications evidence') >= 0 && !that.form.userProbableCommunication) {
        return that.$bvToast.toast("Please enter Communications evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableCommunication) {
        that.form.userProbableCommunication = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Social Networking evidence') >= 0 && !that.form.userProbableSocialNetworking) {
        return that.$bvToast.toast("Please enter Social Networking evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableSocialNetworking) {
        that.form.userProbableSocialNetworking = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Media evidence') >= 0 && !that.form.userProbableMediaEvidence) {
        return that.$bvToast.toast("Please enter Media evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableMediaEvidence) {
        that.form.userProbableMediaEvidence = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Email & Calendar evidence') >= 0 && !that.form.userProbableEmailCalendar) {
        return that.$bvToast.toast("Please enter Email & Calendar evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableEmailCalendar) {
        that.form.userProbableEmailCalendar = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Document evidence') >= 0 && !that.form.userProbableDocuments) {
        return that.$bvToast.toast("Please enter Document evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableDocuments) {
        that.form.userProbableDocuments = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Application Usage evidence') >= 0 && !that.form.userProbableApplicationUsage) {
        return that.$bvToast.toast("Please enter Application Usage evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableApplicationUsage) {
        that.form.userProbableApplicationUsage = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Operating System evidence') >= 0 && !that.form.userProbableOperatingSystem) {
        return that.$bvToast.toast("Please enter Operating System evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableOperatingSystem) {
        that.form.userProbableOperatingSystem = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Encryption and Credentials') >= 0 && !that.form.userProbableEncryptionCredentials) {
        return that.$bvToast.toast("Please enter Encryption and Credentials!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableEncryptionCredentials) {
        that.form.userProbableEncryptionCredentials = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Connected Device evidence') >= 0 && !that.form.userProbableConnectedDevices) {
        return that.$bvToast.toast("Please enter Connected Device evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableConnectedDevices) {
        that.form.userProbableConnectedDevices = 'Not Sought';
      }
      if(that.form.evidenceSought.indexOf('Location & Travel evidence') >= 0 && !that.form.userProbableLocationTravel) {
        return that.$bvToast.toast("Please enter Location & Travel evidence!", {
          title: 'Error',
          variant: 'danger',
          toaster: 'b-toaster-top-center'
        });
      } else if(!that.form.userProbableLocationTravel) {
        that.form.userProbableLocationTravel = 'Not Sought';
      }

      // // Pronouns.
      // if(that.form.userPronouns == 'he/him') {
      //   that.form.pronouns1 = 'his';
      //   that.form.pronouns2 = 'he';
      // } else {
      //   that.form.pronouns1 = 'her';
      //   that.form.pronouns2 = 'she';
      // }

      that.form.hasUnit = (that.form.userUnit) ? true : false;

      // Set the state.
      that.form.stateUpper = that.form.state.toUpperCase();

      // Set up the date and the data for the word doc template.
      const now = new Date();
      const dataset = {
        ...that.form,
        evidenceSoughtStr: '',
        evidenceNotSoughtStr: '',
        evidenceNotSought: defArr,
        images: that.images,
        currentMonth: that.monthsArr[now.getMonth()],
        currentYear: now.getFullYear().toString(),
        myClock: now.toLocaleString('en-US',{hour: 'numeric', minute: 'numeric', hour12:true}),
        userInvestigationFactsList: userInvestigationFactsListDefArr
      };

      // Split this section to lists.
      dataset.userInvestigationFactsList = that.form.userInvestigationFacts.split('\n')

      // Set up the evidence sought.
      that.evidenceSoughtItems.forEach((item: any) => {
        if(that.form.evidenceSought.indexOf(item) == -1) {
          dataset.evidenceNotSought.push(item);
        }
      });

      // Image options for the ImageModule.
      const imageOpts = {
        centered: false,
        getImage: function (tagValue: any, tagName: any): any {
          return new Promise(function (
            resolve,
            reject
          ) {
            PizZipUtils.getBinaryContent(
              tagValue,
              function (error: any, content: any) {
                if (error) {
                  return reject(error);
                }
                return resolve(content);
              }
            );
          });
        },
        getSize: function (img: any, tagValue: any, tagName: any): any {
          // FOR FIXED SIZE IMAGE :
          // return [350, 350];
          return new Promise(function (
              resolve,
              reject
          ) {
              const image = new Image();
              image.src = tagValue;
              image.onload = function () {
                let width = image.width;
                let height = image.height;

                if(width >= 500) {
                  const scale = 500 / width;
                  width = width * scale;
                  height = height * scale;
                }

                  resolve([
                      width,
                      height,
                  ]);
              };
              image.onerror = function (e) {
                  console.log(
                      "img, tagValue, tagName : ",
                      img,
                      tagValue,
                      tagName
                  );
                  alert(
                      "An error occured while loading " +
                          tagValue
                  );
                  reject(e);
              };
          });
        },
      };

      /* *
      * The template's path must be passed as an arguement .
      * that path can be either a URL(as  in the commented line) or a path relative to the Public folder
      * For testing, I have created a folder named  "ReportTemplates" inside the public folder and it contains my sample
      * template named "template1.docx" .
      * */
      // that.loadFile("https://docxtemplater.com/tag-example.docx",function(error: any,content : any){


      that.loadFile('./Word_Template/cellPhone_template.docx', function (error: any, content: any) {
        if (error) {
          throw error
        };

        const zip = new JSZip(content);
        const doc = new Docxtemplater(zip, {
          linebreaks: true,
          modules: [new ImageModule(imageOpts)],
        });

        //  docx generating
        doc.renderAsync(dataset).then(function () {
          const out = doc.getZip().generate({
            type: "blob",
            mimeType: "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
          });
          const fileName = that.form.fileName.replaceAll(' ', '-').replaceAll(' ', '-');
          saveAs(out, `${fileName}.docx`);    // You can pass this blob to a custom file saver component in the project.  
        });
      });
    },
    onReset() {
      const that = this;
      
      this.$bvModal.msgBoxConfirm('Are you sure you want to reset the form?')
        .then(value => {
          if(value) {
            const form: any = that.$refs['myForm'];
            form.reset();
          }
        })
        .catch(err => {
          // An error occurred
        });
      
    }
  },

});
