import dayjs from 'dayjs';
import localizedFormat from 'dayjs/plugin/localizedFormat';

dayjs.extend(localizedFormat);


const defArr: any[] = [];
const photosDefArr: any[] = [];
const imagesDefArr: any[] = [];
const imageUploadsDefArr: any[] = [];
const userInvestigationFactsListDefArr: any[] = [];

export default defineComponent({
  data() {

    return {
      form: {
        userTodayDate: '',

        fileName: '',
        deviceMake: '',
        deviceModel: '',
        deviceColor: '',
        otherDeviceIdentifiers: '',
        evidenceSought: defArr,
        startDate: '',
        endDate: '',
        offenseType: '',
        violationCode: '',
        caseNumber: '',
        userPosition: '',
        userUnit: '',
        hasUnit: false,
        userBackground: '',
        userDepartment: '',
        userDepartmentAddress: '',

        userInvestigationFacts: '',
        userProbableWebRelatedDevice: '',
        userProbableCloudStorage: '',
        userProbablePeerToPeer: '',
        userProbableCommunication: '',
        userProbableSocialNetworking: '',
        userProbableMediaEvidence: '',
        userProbableEmailCalendar: '',
        userProbableDocuments: '',
        userProbableApplicationUsage: '',
        userProbableOperatingSystem: '',
        userProbableEncryptionCredentials: '',
        userProbableConnectedDevices: '',
        userProbableLocationTravel: '',

        userEmail: '',
        userName: '',
        userSignature: '',

        userPronouns: '',
        pronouns1: '',
        pronouns2: '',

        crimeName: '',
        statuteName: '',
        crimeLocation: '',
        locationDescription: '',
        crimeAddress: '',
        caseFacts: '',
        crimeCity: '',
        crimeCounty: '',

        courtType: '',
        courtTypeUpper: '',
        agencyType: '',
        county: '',
        state: '',
        stateUpper: '',
        commissionYear: '',
        trainingAgency: '',
        additionalTraining: '',

        itemsToSeize: '',
        authorizationType: defArr,
        authorizationTypeText: '',
        authorizationAnyTime: '',
        authorizationNoKnock: '',
        authorizationRequested: '',
        authorizationGrantedWarrant: '',
        authorizationReasonablyNecessaryText:'\tAffiant requests authorization to use whatever means as reasonably necessary to access and seize the above-mentioned items.',
        authorizationDocumentText: '\tAffiant requests authorization to measure, photograph, video tape, or otherwise document the aforementioned items.',
        authorizationDayOrNightText: '\tAffiant requests authorization to serve the search warrant at any time of the day or night for the following reason(s): ',
        authorizationNoKnockText: '\tAffiant requests authorization to not be required to knock and announce the presence of law enforcement while serving the search warrant for the following reason(s): ',
        authorizationReasonablyNecessaryWarrantText:'\tLaw enforcement may use any means reasonably necessary to access and seize the above-mentioned items.',
        authorizationDocumentWarrantText: '\tLaw enforcement may measure, photograph, video tape, or otherwise document the aforementioned items.',
        authorizationDayOrNightWarrantText: '\tLaw enforcement may serve this Search Warrant at any time of the day or night as the public interests requires service not to be limited to the daytime hours of 7 a.m. to 8 p.m.',
        authorizationNoKnockWarrantText: '\tLaw enforcement shall not be required to knock and announce their presence during service of this Search Warrant.'

      },
      photos: photosDefArr,
      monthsArr: ['Januray', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
      stateOptions: [
  'Alabama', 'Alaska', 'Arizona', 'Arkansas', 'California', 'Colorado', 'Connecticut', 'Delaware', 'Florida', 'Georgia', 'Hawaii', 'Idaho', 'Illinois', 'Indiana', 'Iowa', 'Kansas', 'Kentucky', 'Louisiana', 'Maine', 'Maryland', 'Massachusetts', 'Michigan', 'Minnesota', 'Mississippi', 'Missouri', "Montana", "Nebraska", "Nevada", "New Hampshire", "New Jersey", "New Mexico", "New York", "North Carolina", "North Dakota", "Ohio", "Oklahoma", "Oregon", "Pennsylvania", "Rhode Island", "South Carolina", "South Dakota", "Tennessee", "Texas", "Utah", "Vermont", "Virginia", "Washington", "West Virginia", "Wisconsin", "Wyoming"],
      authorizationTypeItems: ['Night Time Clause','No Knock Clause'],
      images: imagesDefArr,
      imageUploads: imageUploadsDefArr,
      evidenceSoughtItems: [
        'Web Related evidence',
        'Communication evidence',
        'Social Networking evidence',
        'Media evidence',
        'Email & Calendar evidence',
        'Documents evidence',
        'Cloud Storage evidence',
        'Application Usage evidence',
        'Operating System evidence',
        'Encryption & Credentials',
        'Connected Devices evidence',
        'Location & Travel evidence',
        'Peer to Peer evidence'
      ],
      imageProps: {height: 150, center: true}
    }
  },
  computed: {},
  created() {
    this.form.userTodayDate = dayjs().format('YYYY-MM-DD');
  },
  methods: {
    getDoc() {
      this.createAndSaveDocument();
    },

    deleteSelectedImg: function (selectedPic: any) {
      this.photos = this.photos.filter((item: any) => selectedPic != item.name)
      return;
    },

    loadFile(url: any, callback: any) {
      JSzipUtils.getBinaryContent(url, callback);
    },

    onChange(e: any) {
      const that = this
      const file = e.target.files[0]

      that.photos.push({
        name: file.name,
        pic: file,
        url: URL.createObjectURL(file)
      });
    },
    getPreview(item: any) {
      return URL.createObjectURL(item);
    },
    clearImageUploads() {
      // this.$refs['imageUploads'].reset();
    },
    createAndSaveDocument() {
      const that = this;

      


      // Set up authorization sought
      if(that.form.authorizationType.indexOf('Night Time Clause') >= 0){
        
        that.form.authorizationRequested += that.form.authorizationDayOrNightText;
        that.form.authorizationRequested += that.form.authorizationAnyTime;
        that.form.authorizationRequested += '\r\n\r\n';
        that.form.authorizationGrantedWarrant += that.form.authorizationDayOrNightWarrantText;
        that.form.authorizationGrantedWarrant += '\r\n\r\n';
      }
      if(that.form.authorizationType.indexOf('No Knock Clause') >= 0){

        that.form.authorizationRequested += that.form.authorizationNoKnockText;
        that.form.authorizationRequested += that.form.authorizationNoKnock;
        that.form.authorizationRequested += '\r\n\r\n';
        that.form.authorizationGrantedWarrant += that.form.authorizationNoKnockWarrantText;
        that.form.authorizationGrantedWarrant += '\r\n\r\n';
      }
      
      // Set the court type.
      that.form.courtTypeUpper = that.form.courtType.toUpperCase();

      that.form.hasUnit = (that.form.userUnit) ? true : false;

      // Set the state.
      that.form.stateUpper = that.form.state.toUpperCase();

      // Set up the date and the data for the word doc template.
      const now = new Date();
      const dataset = {
        ...that.form,
        evidenceSoughtStr: '',
        evidenceNotSoughtStr: '',
        evidenceNotSought: defArr,
        images: that.images,
        currentMonth: that.monthsArr[now.getMonth()],
        currentYear: now.getFullYear().toString(),
        myClock: now.toLocaleString('en-US',{hour: 'numeric', minute: 'numeric', hour12:true}),
        userInvestigationFactsList: userInvestigationFactsListDefArr
      };

      // Split this section to lists.
      dataset.userInvestigationFactsList = that.form.userInvestigationFacts.split('\n')

      // Set up the evidence sought.
      that.evidenceSoughtItems.forEach((item: any) => {
        if(that.form.evidenceSought.indexOf(item) == -1) {
          dataset.evidenceNotSought.push(item);
        }
      });

      // Image options for the ImageModule.
      const imageOpts = {
        centered: false,
        getImage: function (tagValue: any, tagName: any): any {
          return new Promise(function (
            resolve,
            reject
          ) {
            PizZipUtils.getBinaryContent(
              tagValue,
              function (error: any, content: any) {
                if (error) {
                  return reject(error);
                }
                return resolve(content);
              }
            );
          });
        },
        getSize: function (img: any, tagValue: any, tagName: any): any {
          // FOR FIXED SIZE IMAGE :
          // return [350, 350];
          return new Promise(function (
              resolve,
              reject
          ) {
              const image = new Image();
              image.src = tagValue;
              image.onload = function () {
                let width = image.width;
                let height = image.height;

                if(width >= 500) {
                  const scale = 500 / width;
                  width = width * scale;
                  height = height * scale;
                }

                  resolve([
                      width,
                      height,
                  ]);
              };
              image.onerror = function (e) {
                  console.log(
                      "img, tagValue, tagName : ",
                      img,
                      tagValue,
                      tagName
                  );
                  alert(
                      "An error occured while loading " +
                          tagValue
                  );
                  reject(e);
              };
          });
        },
      };

      /* *
      * The template's path must be passed as an arguement .
      * that path can be either a URL(as  in the commented line) or a path relative to the Public folder
      * For testing, I have created a folder named  "ReportTemplates" inside the public folder and it contains my sample
      * template named "template1.docx" .
      * */
      // that.loadFile("https://docxtemplater.com/tag-example.docx",function(error: any,content : any){


      that.loadFile('./Word_Template/GenericSearchWarrant.docx', function (error: any, content: any) {
        if (error) {
          throw error
        };

        const zip = new JSZip(content);
        const doc = new Docxtemplater(zip, {
          linebreaks: true,
          modules: [new ImageModule(imageOpts)],
        });

        //  docx generating
        doc.renderAsync(dataset).then(function () {
          const out = doc.getZip().generate({
            type: "blob",
            mimeType: "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
          });
          const fileName = that.form.fileName.replaceAll(' ', '-').replaceAll(' ', '-');
          saveAs(out, `${fileName}.docx`);    // You can pass this blob to a custom file saver component in the project.  
        });
      });
    },
    onReset() {
      const that = this;
      
      this.$bvModal.msgBoxConfirm('Are you sure you want to reset the form?')
        .then(value => {
          if(value) {
            const form: any = that.$refs['myForm'];
            that.form.crimeCounty = '';
            that.form.crimeLocation = '';
            that.form.agencyType = '';
            that.form.courtType = '';
            that.form.trainingAgency = '';
            that.form.userName = '';
            that.form.commissionYear = '';
            that.form.caseNumber = '';
            that.form.caseFacts = '';
            that.form.crimeName = '';
            that.form.statuteName = '';
            that.form.crimeAddress = '';
            that.form.crimeCity = '';
            that.form.locationDescription = '';
            that.form.authorizationType = [];
            that.form.authorizationTypeText = '';
            that.form.authorizationAnyTime = '';
            that.form.authorizationNoKnock = '';
            that.form.authorizationRequested = '';
            that.form.authorizationGrantedWarrant = '';
            form.reset();
          }
        })
        .catch(err => {
          // An error occurred
        });
      
    }
  },

});
