            that.form.userProbableEmailCalendar = '';
            that.form.userProbableDocuments = '';
            that.form.userProbableApplicationUsage = '';
            that.form.userProbableOperatingSystem = '';
            that.form.userProbableEncryptionCredentials = '';
            that.form.userProbableConnectedDevices = '';
            that.form.userProbableLocationTravel = '';
            that.form.deviceMake = '';
            that.form.deviceModel = '';
            that.form.deviceColor = '';
            that.form.otherDeviceIdentifiers = '';
            that.form.startDate = '';
            that.form.endDate = '';
            that.form.startTime = '';
            that.form.endTime = '';
            that.form.timeZone = '';
            that.form.offenseType = '';
            that.form.violationCode = '';
            that.form.evidenceSought = [];
            that.form.evidenceSoughtList = '';
            that.form.userEmail = '';
            that.form.userPronouns = '';
            that.form.pronouns1 = '';
            that.form.pronouns2 = '';
            that.form.deviceDescription = '';
            that.form.propertyNumber = '';
            that.form.itemsToSeize = '';
            that.form.dataProviderCompanyName = '';
            that.form.dataProviderCompanyAddress = '';
            that.form.dataProviderAccountsSought = '';
            that.form.dataProviderNonDisclosure = 'false';
            that.form.fileName = '';
            that.form.userSignature = '';
            that.photos = photosDefArr;
            that.images = imagesDefArr;
            that.imageUploads = imageUploadsDefArr;
            that.clearImageUploads();
            form.reset();
          }
        })
        .catch(err => {
          // An error occurred
        });
      
    }
  },

